.error-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #c78f22;
  background: #303030;
  padding: 10px;
  margin: 20px auto;
  border-radius: 3px;
}

.error-indicator img {
  margin-bottom: 1rem;
}

.error-indicator .boom {
  font-size: 1.7rem;
}